.MarkdownBlog {
    .GlassBg {
        &::before {
            content: "";
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background-color: rgba(255, 255, 255, 0.7);
            z-index: -1;
            filter: blur(3px);
        }
    }
}
